/* hind-regular - latin */
@font-face {
  font-family: "Hind";
  font-style: normal;
  font-weight: 400;
  src: local("Hind Regular"), local("Hind-Regular"),
    url("../../../asset/fonts/hind-v10-latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../../../asset/fonts/hind-v10-latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../../../asset/fonts/hind-v10-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("../../../asset/fonts/hind-v10-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../../../asset/fonts/hind-v10-latin-regular.svg#Hind") format("svg"); /* Legacy iOS */
}

