.container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.container-editor-area {
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-top: 150px;
  background-color: #f9f9f9;
  overflow: hidden;
  /* background: linear-gradient(90deg,  rgb(0,0,0, 0.4) 2%, rgba(0,109,119,0.7) 50%, rgba(0,0,0,0.4) 98%); */
}

.container-editor-area-focusMode {
  /* background: linear-gradient(90deg, rgba(0,109,119,0.6) 10%, rgba(1,118,189,0.7) 50%, rgba(0,109,119,0.6) 90%); */
  background: linear-gradient(
    90deg,
    rgb(0, 0, 0, 0.4) 2%,
    rgba(0, 109, 119, 0.7) 50%,
    rgba(0, 0, 0, 0.4) 98%
  );
}

h3 {
  margin: 10px;
  font-family: "Alegreya Sans";
  font-weight: 600;
  font-size: 1rem;
}

@media screen and (max-width: 450px) {
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.fadeInRight {
  animation-name: fadeInRight;
  animation-duration: 0.85s;
  animation-fill-mode: both;
}

@keyframes fadeInRight {
  0% {
    opacity: 0;
    transform: translateX(80px);
  }

  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}

@media screen and (max-width: 992px) {
  #preview-panel {
    width: auto;
  }
}

.fadeInLeft {
  animation-name: fadeInLeft;
  animation-duration: 0.85s;
  animation-fill-mode: both;
}

@keyframes fadeInLeft {
  0% {
    opacity: 0;
    transform: translateX(-80px);
  }

  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}
